<template>
    <div class="prompt">

        <div class="closeArea">

            <div class="close" style="display:flex;justify-content:flex-end;padding:10px;align-items:center;">
                <div @click="$emit('close')" style="background: rgb(65 184 131);
                padding: 4px 10px 2px 10px;
                border-radius: 20px;">
                    X
                </div>
            </div>

            <div class="container row prompt" style="padding:30px">
             <div class="col-md-5 img-holder">
                <div style="width:100%;">
                    <img src="../assets/img/spin.png" style="width:100%"/>
                </div>
            </div>
            <div class="col-md-7">

                <div class="prompt">
                    <h4>WIN UP TO <span class="attract">N50,000.00</span><br/> INSTANTLY!!!</h4>
                    <p style="text-align: center;font-size: 12px;color: rgb(255 255 255 / 68%);">
                        Must Be <span class="highlight">+18</span> To Play, Enter Your Details And Win Instantly.
                        Here On Koretbet Everybody Wins
                    </p>
                </div>

                <FormulateForm
                v-model="values"
                @submit="handleLogin">
                    <div class="formInput">
                        <FormulateInput type="text" name="phone" class="prompt" label="Phone Number"
                        validation="required|number"/>
                        <FormulateInput type="text" name="email" class="prompt" label="Email"
                        validation="required|email"/>
                    </div>
                <FormulateInput
                type="submit"
                label="Spin Now"/>

