</template>

<script>
import Vue from 'vue';
import Fortune from "./Fortune.vue";

export default {
  name:'PromptUser',
  components:{
      Fortune,
  },
  data(){
      return{
          values:{},
          checkEdUser: false
      }
  },
  methods:{
    handleLogin(data){
       //show modal

       console.log(data);

       this.$store.dispatch("store_user_data",
        JSON.stringify(data));

        if (typeof(Storage) !== "undefined") {

          localStorage.setItem("email", data.email);
          localStorage.setItem("phone", data.phone);

          //store and upload user databasethis.$store.dispatch("store_user_data",
          JSON.stringify(data);

        } else {

          //store and upload user
          this.$store.dispatch("store_user_data",
           JSON.stringify(data));

        }

       console.log("siggnindetails == > :"+ this.$store.getters.signInDetails);

       //perform check to see if user is registered
       //check value against the database

       if (this.checkEdUser == false) {
          this.$emit('close');
          this.checkEdUser = true;
          this.$modal.show(Fortune);
          console.log(data);
       }
    },
  }
}
</script>

