<template>
  <div class="spinner-holder">
    <main><!--confetti-->
    <div class="close" style="display:flex;justify-content:flex-end;padding:10px;align-items:center;">
        <div @click="$emit('close')" style="background: rgb(65 184 131);
        padding: 4px 10px 2px 10px;
        border-radius: 20px;">
            X
        </div>
    </div>
    <div class="title">
        <h4 style="margin-bottom: 18px;font-size: 19px;color: white!important;text-align:center; margin-top:opx!important;">
          SPIN NOW!! <span style="color: white!important;font-size: 20px;"></span></h4>
    </div>

      <FortuneWheel
        style="width: 300px"
        type="image"
        :useWeight="true"
        :prizes="prizes"
        :prizeId="1"
        :angleBase="-10"
        @rotateStart="onImageRotateStart"
        @rotateEnd="onRotateEnd"
      >
        <img slot="wheel" src="@/assets/img/spin.png" />
        <img slot="button" src="@/assets/img/button1.png" />
      </FortuneWheel>

    </main>
  </div>
</template>

<script>
import Vue from 'vue'
import Redeem from "./Redeem.vue";
import FortuneWheel from 'vue-fortune-wheel'
import 'vue-fortune-wheel/lib/vue-fortune-wheel.css'
import VueConfetti from 'vue-confetti'

Vue.use(VueConfetti)

export default {
  components: {
    FortuneWheel, Redeem
  },
  computed:{
    priceRedeemed(){
      return this.$store.getters.priceRedeemed;
    }
  },
  created(){

  },
  data() {
    return {
      cavansVerify: true, // Whether the turntable in canvas mode is enabled for verification
      canvasOptions: {
        borderWidth: 6,
        borderColor: '#584b43'
      },
      disabled:false,
      prizes: [
        {
          id: 1, //* The unique id of each prize, an integer greater than 0
          name: 'Blue', // Prize name, display value when type is canvas (this parameter is not needed when type is image)
          value: 'Blue\'s value', //* Prize value, return value after spinning
          bgColor: '#45ace9', // Background color (no need for this parameter when type is image)
          color: '#ffffff', // Font color (this parameter is not required when type is image)
          probability: 30, //* Probability, up to 4 decimal places (the sum of the probabilities of all prizes
          weight: 1 // Weight, if useWeight is true, the probability is calculated by weight (weight must be an integer), so probability is invalid
        },
        {
          id: 2,
          name: 'Red',
          value: 'Red\'s value',
          bgColor: '#dd3832',
          color: '#ffffff',
          probability: 40,
          weight: 1
        },
        {
          id: 3,
          name: 'Yellow',
          value: 'Yellow\'s value',
          bgColor: '#fef151',
          color: '#ffffff',
          probability: 30,
          weight: 1
        }
      ],
      value:"SPIN NOW!!!",
      wins:"",
      spinOnce:false,
    }
  },
  methods: {
    onImageRotateStart() {
      console.log('onRotateStart');
      this.spinOnce = true;
    },
    onCanvasRotateStart(rotate) {
      if (this.canvasVerify) {
        const verified = true // true: the test passed the verification, false: the test failed the verification
        this.DoServiceVerify(verified, 2000).then((verifiedRes) => {
          if (verifiedRes) {
            console.log('Verification passed, start to rotate')
            rotate() // Call the callback, start spinning
            this.canvasVerify = false // Turn off verification mode
          } else {
            alert('Failed verification')
          }
        })
        return
      }
      console.log('onCanvasRotateStart')
    },
    onRotateEnd(prize) {

      this.disabled = true;
      this.$confetti.start();

      this.$emit('close');
      this.$modal.show(Redeem);

      //close confetti
      setInterval(() => {
        //stop confetti
        this.$confetti.stop();
      }, 6000);
    },
    // Simulate the request back-end interface, verified: whether to pass the verification, duration: delay time
    DoServiceVerify(verified, duration) {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve(verified)
        }, duration)
      })
    },
    signIn(){
      this.$emit('close');
    }
  }
}
</script>

<style>
  .spinner-holder{
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(44 61 88);
    padding: 22px;
}

</style>
