<template>
  <div>
    <Header/>
    <Categories class="cate"/>
  </div>
</template>


<script>
  // @ is an alias to /src
  import Header from '../components/Header.vue'
  import PromptUser from '../components/PromptUser.vue'
  import Categories from "../components/Categories.vue";

  export default {
    name: 'Home',
    components: {
      Header, Categories, PromptUser
    },
    created(){
  		//show modal
  		if(this.$store.getters.shownModal == false){
  			this.showmodal();
  		}else{
  			console.log(this.$store.getters.shownModal);
  		}
  	},
    methods:{
      showmodal(){
        this.$store.dispatch('get_shown_modal', true);
        this.$modal.show(PromptUser);
      },
    }
  }
</script>

<style>
  .cate{
		z-index:-1;
		margin-top: -50px;
	}
</style>
