import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    shownModal: false,
    spinnedOnce: false,
    validUser: false,
    showSignUp: false,
    userDetails: {},
    signInDetails: {},
    priceRedeemed: false,
    onceperuser:false,
  },
  mutations: {
    UPDATE_ONCE_PER_USER(state, payload){
      state.onceperuser = payload;
    },
    UPDATE_PRICE_REDEEMED(state, payload){
      state.priceRedeemed = payload;
    },
    UPDATE_SHOW_SIGN_UP(state, payload) {
        state.showSignUp = payload;
    },
    UPDATE_SHOWN_MODAL(state, payload) {
        state.shownModal = payload;
    },
    UPDATE_SPINNED_ONCE(state, payload) {
        state.spinnedOnce = payload;
    },
    UPDATE_VALID_USER(state, payload) {
        state.validUser = payload;
    },
    UPDATE_USER_DETAILS(state, payload) {
        state.userDetails = payload;
    },
    UPDATE_SIGNIN_DETAILS(state, payload) {
        state.signInDetails = payload;
    }
  },
  actions: {
    get_once_per_user({commit}, value){
      commit("UPDATE_ONCE_PER_USER", value );
    },
    priceRedeemed({commit}){
      commit("UPDATE_PRICE_REDEEMED", false);
    },
    get_show_sign_up({ commit }, value) {
        commit('UPDATE_SHOW_SIGN_UP', value);
    },

    get_shown_modal({ commit }, value) {
        commit('UPDATE_SHOWN_MODAL', value);
    },

    get_spinned_once({ commit }, value) {
        commit('UPDATE_SPINNED_ONCE', value);
    },

    store_signup_user_signup_data({ commit }, value) {
        axios.post('api/user/signup' + value)
            .then((response) => {
                //commit user details here
                //do whatever you want here
                commit('UPDATE_VALID_USER', response.user);
            })
            .catch(error => {
                //catch your errors here
                console.log(error);
            });
    },

    store_user_data({ commit }, value) {
        axios.post('api/user/signup' + value)
        .then((response) => {
            //commit user details here
            //do whatever you want here
            commit('UPDATE_SIGNIN_DETAILS', response.user);
        })
        .catch(error => {
            //catch your errors here
            console.log(error);
        });
        // you can move this commit into promise
        commit('UPDATE_SIGNIN_DETAILS', value);
    }
  },
  getters:{
    shownModal: state => state.shownModal,
    spinnedOnce: state => state.spinnedOnce,
    validUser: state => state.validUser,
    userDetails: state => state.userDetails,
    signInDetails: state => state.signInDetails,
    showSignUp: state => state.showSignUp,
    priceRedeemed: state => state.priceRedeemed,
    onceperuser: state => state.onceperuser
  },
  modules: {
  }
})
