<template>
  <div v-if="showRedeemed" style="background: rgb(35, 49, 70);
    padding: 10px;">

    <div style="width: 100%;
    display: flex;
    justify-content: flex-end;">
      <div @click="$emit('close')" style="background: rgb(65, 184, 131);
      padding: 4px 10px 2px;
      border-radius: 20px;
      text-align: end;
      width: 28px;">
          X
      </div>
    </div>

    <h4 style="margin-bottom: 18px;font-size: 19px;color: white!important;text-align:center;">
    You've Won<span style="color: white!important;font-size: 20px;"> N1,000</span></h4>

    <div style="display:flex;justify-content:center;" >
      <router-link @click="$emit('close')" class="white_bg_btn" to="/signup">REDEEM Now</router-link>
    </div>
  </div>
</template>

<script>


  export default{
    name:"Redeem",
    created(){
      setInterval(() => {
        this.$emit('close');
        this.showRedeemed = false;
        if(!this.$store.getters.onceperuser){
          this.$router.push("/signup").catch(()=>{});
          this.$store.dispatch("get_once_per_user", true);
        }

      }, 4000);
    },
    data(){
      return{
        showRedeemed:true,
      }
    },
    methods:{

    }
  }
</script>
